import React, { useContext, useState } from 'react'
import "../css/Contact.css"
import paintbrush from "../../designassets/Frame.png"
import { LocationContext } from "../../context/LocationContext"

const Contact = () => {
    const [input, setInput] = useState('')
    const [search, setSearch] = useState('')
    const [info, setInfo ] = useContext(LocationContext)

    const find = () => {
        setSearch(input)
    }
    
    return (
        <div className="contact-container" style={{position:"absolute", left: "50%", transform: "translateX(-50%)"}} >
            <h1>Contact Us</h1>
            <div className="contact-search">
                <input type="text" placeholder="Enter your Country" onChange={e => setInput(e.target.value)}/>
                <button onClick={find}>Find</button>
            </div>
            <section className="emails">
                <h3>Emails</h3>
                <ul>
                    <li>paintmagicint@gmail.com</li>
                    <li>paintmagicusa@gmail.com</li>
                    <li>paintmagicuk@gmail.com</li>
                    <li>paintmagicaus@gmail.com</li>
                    {info.map(data => {
                        if(data.country.toLowerCase() == search.toLowerCase()){
                           return data.emails.map(email => {
                                return <li>{email}</li>
                            })
                        }
                    })}
                </ul>
            </section>
            <section className="numbers">
                <h3>Numbers</h3>
                <ul>
                    <li>+ 67 456 742 7539</li>
                    <li>+ 12 459 125 743</li>
                    <li>+ 13 159 759 451</li>
                    <li>+ 50 485 743 756</li>
                    {info.map(data => {
                        if(data.country.toLowerCase() == search.toLowerCase()){
                           return data.numbers.map(number => {
                                return <li>{number}</li>
                            })
                        }
                    })}
                </ul>
            </section>
            {/* <div className="paintbrush">
                <img src={paintbrush} alt="" width="120px" height="300px"/>
            </div> */}
        </div>
    )
}

export default Contact
