import React from 'react'
import "../css/Reviews.css"

const reviews = [
    {
        date: "12/12/12",
        review: "this is the best art school in the world. my son has discovered his inner Picasso. The teacher do more than just teach art they teach life and care deeply about their students.",
        person: "James Macbook"
    },
    {
        date: "12/08/20",
        review: "this is the best art school in the world. my son has discovered his inner Picasso. The teacher do more than just teach art they teach life and care deeply about their students.",
        person: "Lily Samsung"
    },
    {
        date: "01/04/15",
        review: "this is the best art school in the world. my son has discovered his inner Picasso. The teacher do more than just teach art they teach life and care deeply about their students.",
        person: "Arthur Huawei"
    },
    {
        date: "31/10/17",
        review: "this is the best art school in the world. my son has discovered his inner Picasso. The teacher do more than just teach art they teach life and care deeply about their students.",
        person: "Michelle Microsoft"
    }
]

const Reviews = () => {
    return (
        <div className="reviews-container" style={{position:"absolute", left: "50%", transform: "translateX(-50%)"}}>
            <h1>Reviews</h1>
            <div className="review-wrapper">
                {
                    reviews.map(review => {
                        return  <figure key={review.date}>
                                    <span>{review.date}</span>
                                    <blockquote>{review.review}</blockquote>
                                    <figcaption>{review.person}</figcaption>
                                </figure>
                    })
                }
            </div>
           
        </div>
    )
}

export default Reviews
