import React from 'react'
import "../css/Apply.css"

const Apply = () => {
    
    const fileText = document.getElementById("file-text")
    const file = document.getElementById("file")
    const upload = () => file.click()
    const filename = () => file.value ? fileText.textContent = file.value : fileText.textContent = "no file chosen"
    
    return (

        <div className="apply-container" style={{position:"absolute", left: "50%", transform: "translateX(-50%)"}}>
            <h1>Application Form</h1>
            <form action="">
                <div  className="normal">
                    <label htmlFor="fullname">fullname</label>
                    <input type="text"/>
                </div>

                <div  className="normal">
                    <label htmlFor="email">email</label>
                    <input type="text"/>
                </div>

                <div  className="normal">
                    <label htmlFor="number">number</label>
                    <input type="text"/>
                </div>

                <div className="highest">
                    <label htmlFor="highest">highest education</label>
                    <select name="highest" id="">
                        <option value="">high school</option>
                        <option value="">certificate</option>
                        <option value="">diploma</option>
                        <option value="">bachelor's degree</option>
                        <option value="">honor's degree</option>
                        <option value="">master's degree</option>
                        <option value="">doctorate</option>
                    </select>
                </div>

                <div className="school-list">
                    <label htmlFor="schools">previous schools</label>
                    <div>
                        <input type="text"/>
                        <input type="text"/>
                        <input type="text"/>
                        <input type="text"/>
                    </div>
                </div>

                <div className="files">
                    <label htmlFor="files">upload latest results / certificates</label>
                    
                    <div>
                    <input type="file" name="files" id="" placeholder="file" hidden="hidden" id="file" onChange={filename}/>
                        <button type="button" onClick={upload}>files</button>
                        <span id="file-text">no file chosen</span>
                    </div>
                    
                </div>
                <button className="apply-button">Apply</button>
            </form>
        </div>
    )
}

export default Apply
