import React, { useContext } from 'react'
import "../css/Locate.css"
import { LocationContext } from "../../context/LocationContext"

const Locate = () => {
    const [info, setInfo] = useContext(LocationContext)
    console.log(info)
    return (
        <div className="locate-container" style={{position:"absolute", left: "50%", transform: "translateX(-50%)"}}>
            <h1>Locate Us</h1>
            <div className="locatepaintmark"></div>
            {info.map(countries => {
               return <section className="locate-section">
                    <h3>{countries.country}</h3>
                        <ul>
                            {countries.address.map(adres => {
                               return <li>{adres}</li>
                            })}
                        </ul>
                </section>
            })}
           
        </div>
    )
}

export default Locate
