import React from 'react'
import "../css/Art.css"
import img1 from "../../art/1.jfif"
import img2 from "../../art/2.jfif"
import img3 from "../../art/3.jfif"
import img4 from "../../art/4.jfif"
import img5 from "../../art/5.jfif"
import img6 from "../../art/6.jfif"



const art = [
    {
        name: "Sam Header",
        desc: "this was inspired by the subway sam’s mom takes to work everyday and is made to depict how we are endlessly wondering in the universe",
        img:img1
    },
    {
        name: "John Body",
        desc: "this was inspired by the subway sam’s mom takes to work everyday and is made to depict how we are endlessly wondering in the universe",
        img:img2
    },
    {
        name: "Eric Section",
        desc: "this was inspired by the subway sam’s mom takes to work everyday and is made to depict how we are endlessly wondering in the universe",
        img:img3
    },
    {
        name: "Angela Footer",
        desc: "this was inspired by the subway sam’s mom takes to work everyday and is made to depict how we are endlessly wondering in the universe",
        img:img4
    },
    {
        name: "Doil Div",
        desc: "this was inspired by the subway sam’s mom takes to work everyday and is made to depict how we are endlessly wondering in the universe",
        img:img5
    },
    {
        name: "Ann Anchor",
        desc: "this was inspired by the subway sam’s mom takes to work everyday and is made to depict how we are endlessly wondering in the universe",
        img:img6
    },
]


const Art = () => {
    return (
        <div className="art-container" style={{position:"absolute", left: "50%", transform: "translateX(-50%)"}}>
            <h1>Art Work</h1>
            <h3>created by students</h3>
            <ol>
                {art.map(piece => {
                    return  <li key={piece.name}>
                                <h2>{piece.name}</h2>
                                <p>{piece.desc}</p>
                                <img src={piece.img} alt=""/>
                            </li>
                })}
            </ol>
        </div>
    )
}

export default Art
