import React from 'react'
import "../css/About.css"

import { FaFacebookSquare, FaTwitter, FaYoutube } from "react-icons/fa"

const About = () => {
    return (
        <div className="about-container" style={{position:"absolute", left: "50%", transform: "translateX(-50%)"}}>
            <section className="about-us">
                <h1>About Us</h1>
                <div className="rotate">
                    <div>
                        here at the paint magic school of arts we will teach you magic. We are the most innovative school of arts in the country with our unique and groundbreaking curriculum desgined to push your potential to new levels of creativity and design
                    </div>
                </div>
            </section>

            <section className="curriculum">
                <h1>Our Curriculum</h1>
                <div>
                Our moto is simple: If you want to be the best you have to learn from the best. Our curriculum has been create by the most influential artists of our time to amd we regularly update it with the latest trends. We strive to deliver the education in the world.
                </div>
            </section>

            <section className="faculty">
                <h1>Our Faculty</h1>
                <div >
                
                     The teachers at Paint Magic are some of the best painters, writers, dancers and musicians in the world! We believe if you want to be the best you have to learn from the best. Our teachers take pride in their work to produce world class artists
            
                </div>
            </section>

            <section>
                <h4>Don't forget to follow us!</h4>
                <div id="about-icon-container">
                    <FaFacebookSquare color="#3b5998"/>
                    <FaTwitter color="#1DA1F2"/>
                    <FaYoutube color="#c4302b"/>
                </div>
            </section>
        </div>
    )
}

export default About
