import './App.css';
import { useContext } from "react"
import { Switch, Route } from "react-router-dom"
import { useTransition, animated } from "react-spring"
import { __RouterContext } from "react-router"

import Home from "./components/jsx/Home"
import About from "./components/jsx/About"
import Header from "./components/jsx/Header"
import Footer from "./components/jsx/Footer"
import Contact from "./components/jsx/Contact"
import Locate from "./components/jsx/Locate"
import Funding from "./components/jsx/Funding"
import Art from "./components/jsx/Art"
import Reviews from "./components/jsx/Reviews"
import Apply from "./components/jsx/Apply"
import {LocationProvider} from "./context/LocationContext"




function App() {
  const { location } = useContext(__RouterContext)
  console.log(__RouterContext)

  const transitions = useTransition(location, location => location.pathname, {
    from: {opacity:0, transform: "translateY(100%)"},
    enter: {opacity:1, transform: "translateY(0)"},
    leave: {opacity:0, transform: "translateY(-100%)"},
    config: {
      duration: 500
    }
  })
  return (
    <LocationProvider>
    
    <div className="App">
      <Header/>

      {transitions.map(({item, props, key}) => (
        <animated.div key={key} style={props} >
        <Switch location={item} >
          <Route exact path="/" component={Home}/>
          <Route exact path="/about" component={About}/>
          <Route exact path="/contact" component={Contact}/>
          <Route exact path="/location" component={Locate}/>
          <Route exact path="/funding" component={Funding}/>
          <Route exact path="/art" component={Art}/>
          <Route exact path="/reviews" component={Reviews}/>
          <Route exact path="/apply" component={Apply}/>
        </Switch>
      </animated.div>
       ))} 
     
      <Footer/>
    </div>
    </LocationProvider>
  );
}

export default App;
